import "../styles/index.css";
import localFont from "next/font/local";
// localFont irá incluir a font para uso

const jetBrains = localFont({
  src: "../fonts/JetBrains.ttf", //Caminho na font
  variable: "--font-geist-regular", //Definindo uma váriavel css que irá ter como valor a font do src,
});

export default function RootLayout({
  children,
}: Readonly<{
  children: React.ReactNode;
}>) {
  return (
    <html lang="en">
    <body className="font-geist">{children}</body>
    </html>
  );
}
